.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 19px;
  color: #2d2d2d;
  padding: 4px 109px;
  background: #ffffffcf;
  position: sticky;
  top: 0;
  box-shadow: 0 3px 18px 0 rgba(64, 23, 0, 0.5);
  z-index: 99;
  a {
    cursor: pointer;
  }
  .logo {
    height: 70px;
  }
  .menu {
    display: flex;
    span {
      margin: 24.5px 0 24.5px 42px;
      -webkit-font-smoothing: antialiased;
      cursor: pointer;
    }
  }
  .user {
    height: 60px;
    img {
      border-radius: 8px;
      height: 50px;
      margin: 5px;
    }
  }
}

.btn {
  display: inline-block;
  background: black;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}
